@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  padding: 2rem;
}

.Pokegame {
  display: flex;
  background: #70e1f5; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #ffd194,
    #70e1f5
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
@media (max-width: 724px) {
  .Pokegame {
    display: block;
  }
}

.Pokedex {
  border-left: 6px solid black;
  border-right: 6px solid black;
}
.Pokedex-content {
  /* display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr; */
  padding: 0 4rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.Pokedex-item {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}
.Pokedex > h3 {
  padding: 1.5rem;
  margin: 0;
  text-align: center;
}
.win {
  color: white;
  background: #76b852; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #8dc26f,
    #76b852
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.lose {
  color: white;
  background: #cb356b; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to top,
    #bd3f32,
    #cb356b
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.Pokecard {
  border-radius: 5px;
  margin: 4rem 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
  margin-top: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  transition: all ease 500ms;
}
.Pokecard:hover {
  transform: scale(1.1);
}

.Pokecard-item {
  line-height: 0.7rem;
  margin: 0;
  padding: 0.5rem;
  text-transform: uppercase;
}

.Pokecard-img {
  height: 150px;
  transition: all ease 500ms;
}
.Pokecard-img:hover {
  transform: scale(1.3);
}

